.product {
    border: 1px solid #ddd;
    padding: 20px;
    margin: 10px;
    text-align: center;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s;
  }
  
  .product:hover {
    transform: scale(1.05);
  }
  
  .product img {
    max-width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
    margin-bottom: 15px;
  }
  
  .product-info {
    margin-top: 10px;
  }
  
  .product-info h2 {
    font-size: 22px;
    margin-bottom: 10px;
  }
  
  .product-info p {
    font-size: 20px;
    color: #b12704;
    margin-bottom: 15px;
  }
  
  .product-info button {
    background-color: #ff9900;
    border: 1px solid #a88734;
    padding: 10px 25px;
    cursor: pointer;
    color: white;
    font-weight: bold;
  }
  
  .product-info button:hover {
    background-color: #e68a00;
  }
  