.navbar {
    display: flex;
    justify-content: space-between;
    background-color: #232f3e;
    padding: 10px;
    color: white;
  }
  
  .navbar-logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .navbar-links {
    list-style-type: none;
    display: flex;
    gap: 20px;
  }
  
  .navbar-links li {
    cursor: pointer;
  }
  